<template>
    <div class="border-bottom" v-if="items.length > 0">
        <div class="columns col-oneline medium page-container ">
            <div class="column col-4 col-md-6 col-sm-12" v-for="i in items">
                <a :href="i.url" class="content medium-item">
                    <div class="content-body img-background medium-item__image" :style="`background:url(${i.img})`"> </div>
                    <div class="content-footer h5 text-bold content-padding medium-item__title">
                        {{i.title}}
                    </div>
                </a>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data: () => ({
            items: []
        }),
        mounted() {
            this.posts()
        },
        methods: {
            posts() {
                fetch('https://api.rss2json.com/v1/api.json?rss_url=https://medium.com/feed/marcius-studio')
                    .then((res) => res.json())
                    .then((data) => {
                        // Fillter the array
                        if (data && data.items) {
                            const res = data.items //This is an array with the content. No feed, no info about author etc..
                            const posts = res.filter(i => i.categories.length > 0 && i.thumbnail) // That's the main trick* !

                            this.items = posts.map(i => ({ title: i.title, url: i.link, img: i.thumbnail }))
                        }
                    })
            }
        }
    }
</script>

<style lang="scss">

    .medium-item {
        .medium-item__image {
            height: 10rem;
        }
        .medium-item__title {
            padding-bottom: 0;
        }
    }
</style>