<template>
    <div>

        <preview :i="item" class="case-page h100" />

        <component :is="componentId"></component>

        <block-medium />
        <!--footer block-->
        <block-footer />

    </div>
</template>

<script>
    import schema from '../modules/schema'

    import blockMedium from './modules/medium'

    // componnets
    import cryptoScale from './components/crypto-scale'
    import sovdex from './components/sovdex'
    import whaleAlerts from './components/whale-alerts'
    import nightInTechnopark from './components/night-in-technopark'
    import childhoodSupport from './components/childhood-support'

    export default {
        data: () => ({
            componentId: 'sovdex',
        }),
        computed: {
            item() {
                const id = this.componentId = this.$route.params.id
                return schema.find(i => i.id == id)
            }
        },
        mounted() {
            const text = this.item.name.replace(/<[^>]*>?/gm, ' ') || 'Case'
            document.title = (text) + ` - Marcius Studio`
            this.$route.meta.breadcrumb = text
        },
        components: {
            // blocks
            blockMedium,

            // cases
            'crypto-scale': cryptoScale,
            sovdex,
            whaleAlerts,
            nightInTechnopark,
            childhoodSupport,
        }
    }
</script>